/*
* Style Works for both Editor and Frontend
*/

.wp-block-aab-group-accordion {
	&.horizontal_filter {
		.aab-filter-button-group {
			flex-direction: row;
		}
	}

	&.vertical_filter {
		position: relative;
		padding-left: 225px !important;
		min-height: 400px;

		.aab-filter-button-group {
			position: absolute;
			left: 0;
			top: 0;
			flex-direction: column;
			width: 200px;
		}
	}

	.aab-filter-button-group {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 25px;
		gap: 10px;

		&.right_btn {
			justify-content: end;
		}

		&.center_btn {
			justify-content: center;
		}

		&.left_btn {
			justify-content: flex-start;
		}

		.btn {
			font-size: 14px;
			font-weight: 400;
			outline: none;
			border: 1px solid;
			border-radius: 4px;
			padding: 0 20px;
			line-height: 28px;
			margin: 0;
			transition: all 0.3s linear;
			text-decoration: none;
			cursor: pointer;

			color: #0866ff;
			background-color: #f4f8ff;
			border-color: #0866ff33;

			&:hover {
				color: #0866ff;
				background-color: #dde9fd;
				border-color: #0866ff33;
			}

			&.active,
			&.active:hover {
				color: #f4f8ff;
				background-color: #0866ff;
				border-color: #0866ff;
				cursor: default;
			}
		}
	}

	.aab-show-more-btn-container {
		margin-top: -50px;
		padding-top: 50px;
		display: flex;
		justify-content: center;

		.show-more-btn,
		.show-less-btn {
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 14px;
			outline: 3px solid #00000000;
			border: none;
			padding: 10px 20px;
			border-radius: 50px;
			background-color: #eaeaea;
			color: #010101;
			cursor: pointer;
			&:hover {
				opacity: 80%;
			}
			&:active {
				transition-duration: 300ms;
				scale: 95%;
			}
		}

		button.show-less-btn {
			display: none;
		}
	}

	.aagb__accordion_container {
		&.no-pro-plan {
			.aagb__accordion_body {
				padding: 10px !important;
			}
		}

		&.step {
			.aagb__accordion_icon {
				width: auto;
				height: auto;
			}
		}

		&.style-qa {
			&.step {
				.aagb__accordion_body {
					.continue {
						width: 90%;
						margin-bottom: 10px;
					}
				}
			}

			&.aagb__accordion_active {
				.aagb__accordion_head {
					:active {
						border-bottom: none !important;
					}

					.aagb__accordion_heading {
						.icon-container {
							.icon-q {
								border-radius: 3px 3px 0 0;
							}
							.icon-a {
								top: 43px;
								opacity: 1;
							}
						}
					}
				}
			}

			&.check-list {
				.aagb__accordion_body {
					padding-left: 150px;
				}
			}

			.aagb__accordion_head {
				padding: 10px 20px;

				.aagb__accordion_heading {
					gap: 10px;
				}
			}

			.aagb__accordion_body {
				padding: 0 20px 10px 110px;
				border-top: none !important;

				.block-editor-inner-blocks {
					.block-editor-block-list__layout {
						:first-child {
							margin-top: 0;
						}
					}
				}
			}

			.aagb__accordion_body.aagb__accordion_body--show {
				border-top: none !important;
				transition: border-top-color 0.3s linear;

				.aagb__accordion_component :first-child {
					margin-top: 0;
				}
			}

			:where(.wp-site-blocks *:focus) {
				outline-width: 0;
				outline-style: none;
			}
		}

		&.check-list {
			.aagb__accordion_head {
				.aagb__accordion_heading {
					.checklist-box {
						width: 25px;
						height: 25px;
						-moz-appearance: none;
						appearance: none;
						-webkit-appearance: none;
						border: 1px solid #e6e6e6;
						background-color: #fff;
						cursor: pointer;
						margin-right: 10px;
						display: flex;
						justify-content: center;
						align-items: center;
						border-radius: 4px;
						transition: transform 0.3s ease-in-out;
					}

					/* Optional: Checkmark Icon (using pseudo-elements) */
					.checklist-box:checked::after {
						content: "âœ“";
						display: block;
						text-align: center;
						font-size: 16px;
						color: #0707a3;
					}

					.checklist-box:focus {
						outline: none;
					}
				}
			}
		}
	}

	.step-result {
		border: 1px solid #3e94b2;
		display: none;
		padding: 14px 20px;
	}
}

.wp-block-aab-accordion-item {
	overflow: hidden;
	transition: all 0.3s linear;
	border-radius: 6px;

	&.aagb__accordion_active {
		overflow: hidden;
	}

	.highlight,
	mark {
		background: yellow !important;
		padding: 0 !important;
		margin: 0 !important;
	}

	.aagb__accordion_head {
		display: flex;
		align-items: center;
		justify-content: space-between;
		cursor: pointer;
		outline: 0;
		padding: 13px 22px;

		.head_content_wrapper {
			transition: all 0.3s ease-in-out;
		}

		.heading-icon {
			margin-right: 18px;

			img {
				max-width: 50px;
				height: auto;
				box-shadow: none;
			}
		}

		// Step css
		.complete-sign {
			background-color: #18b36f;
			color: #fff;
			border-radius: 50%;
			text-align: center;
			font-size: 15px;
			display: none;
			height: 24px;
			width: 24px;
			line-height: 26px;
		}

		// header icon
		.aagb__accordion_icon {
			border-radius: 50%;
			.aagb__icon_dashicons_box {
				display: flex;
				align-items: center;
				justify-content: center;
				.aagb__icon {
					height: auto;
					width: auto;
				}
			}
		}

		// header icon position
		&.aagb_right_icon {
			flex-direction: row;
		}

		&.aagb_left_icon {
			flex-direction: row-reverse;
		}

		// heading
		.aagb__accordion_heading {
			display: flex;
			flex: 1;
			align-items: center;
			transition: all 0.3s ease-in-out;

			.aagb__accordion_subheading {
				margin: 0;
				transition: all 0.2s ease-in-out;
				opacity: 0.8;
			}

			&.aagb_right_icon {
				margin-right: 20px;
				flex: 1;
			}

			&.aagb_left_icon {
				margin-left: 20px;
				flex: 1;
			}

			&.aagb_left_link {
				.title_wrapper {
					flex-direction: row-reverse;
				}
			}

			.title_wrapper {
				position: relative;
				display: flex;
				align-items: center;
				gap: 10px;
			}

			.aagb__accordion_title.line-through {
				text-decoration: line-through;
				color: #848d95;
			}

			.aagb__accordion_title {
				padding-top: 0;
				padding-bottom: 0;
			}

			.aagb__labels_container {
				display: flex;
				align-items: center;
				gap: 4px;

				.label_name {
					font-size: 12px;
					padding: 4px 8px;
					border-radius: 20px;
					color: #1570ec;
					background-color: #c1d8f7;

					display: inline-flex;
					align-items: center;
					justify-content: center;
				}
			}

			a {
				text-decoration: none;
				opacity: 0.5;
				font-size: 1.4rem !important;
				line-height: 0.5;
				padding-top: 0;

				&.anchorjs-link {
					&:after {
						font-size: 1.4rem !important;
					}
				}
			}

			&:hover {
				a {
					opacity: 1;
				}
			}
		}

		.icon-container {
			display: block;
			position: relative;
			.icon-q {
				background: #505050;
				color: #fff;
				width: 80px;
				line-height: 43px;
				font-size: 25px;
				text-align: center;
				border-radius: 3px;
				font-weight: 500;
				position: relative;
				z-index: 2;
			}
			.icon-a {
				background: #f5a623;
				color: #fff;
				width: 80px;
				line-height: 43px;
				font-size: 25px;
				text-align: center;
				border-bottom-left-radius: 3px;
				border-bottom-right-radius: 3px;
				font-weight: 500;
				position: absolute;
				top: 0;
				left: 0;
				opacity: 0;
				transition: all 0.4s ease-in-out;
				z-index: 1;
			}
		}
	}

	.collapse-icon span {
		display: block;
	}

	.aagb__accordion_body {
		display: none;
		padding: 13px 22px;
		transition: border-top-color 0.3s linear;

		&.aagb__accordion_body--visible {
			display: block !important;
		}

		// Step css
		.continue {
			background: #1570ec;
			border-radius: 3px;
			color: #ffffff;
			padding: 8px 18px;
			cursor: pointer;
			display: flex;
			justify-content: space-between;

			.step-down-icon {
				color: #41a9f8;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;

				svg {
					width: 20px;
					height: 20px;
				}
			}
		}

		.aagb_button_toggle {
			padding: 0px;
			border: 0px;
			color: #333;
			font-size: 16px;
			font-weight: 500;
			background: transparent;
			border: 0px;

			&:hover {
				color: #4c4cf1;
				text-decoration: underline;
			}

			&:focus {
				outline: none;
				box-shadow: none;
			}
		}

		.aagb__accordion_component {
			position: relative;

			p {
				margin-bottom: 20px;
			}

			p:last-child {
				margin-bottom: 4px;
			}

			.aagb_overlay {
				content: "";
				position: absolute;
				background: linear-gradient(
					to bottom,
					#ffffff00,
					rgba(#ffffff, 0.9)
				);
				height: 50px;
				right: 0;
				bottom: -5px;
				left: 0;
				width: 100%;
				z-index: 2;
			}
		}
	}
}

.aagb_form_inner {
	max-width: 600px;
	width: 100%;
	margin: 0 auto 35px;

	.aagb_form_group {
		position: relative;

		&:before {
			position: absolute;
			left: 18px;
			font-size: 24px;
			color: var(--brand_color, #00a19a);
			top: 50%;
			transform: translateY(-50%);
			content: "\f179";
			font-family: dashicons;
		}
	}

	.aagb_form_control {
		font-size: 14px;
		font-weight: 400;
		height: 50px;
		line-height: 50px;
		box-shadow:
			0 1px 1px rgba(11, 80, 115, 0.3),
			0 1px 5px rgba(60, 64, 67, 0.1);
		padding-right: 30px;
		background: #fff;
		border: 0;
		padding-left: 52px;
		width: 100%;
		border-radius: 45px;
		color: #242729;

		&::placeholder {
			color: #848d95 !important;
		}
	}

	input[type="search"]::-webkit-search-cancel-button {
		-webkit-appearance: searchfield-cancel-button !important;
		height: 18px;
		width: 18px;
		margin-left: 0.4em;
		background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='%23777'><path d='M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z'/></svg>");
		cursor: pointer;
	}

	.help-block {
		font-size: 16px;
		display: block;
		text-align: center;
		margin-top: 6px;
	}
}

.aagb_accordion_wrapper_btn {
	display: flex;

	&.right_btn {
		justify-content: flex-end;
	}

	&.left_btn {
		justify-content: flex-start;
	}

	&.center_btn {
		justify-content: center;
	}

	a {
		margin: 0 0 12px 16px;
		color: #333;
		font-weight: 500;
		font-size: 16px;
		text-decoration: none !important;
		display: flex;

		&:hover {
			color: #069;
		}

		svg {
			width: 0.875em;
			margin-right: 4px;
		}
	}
}

.label-d-block {
	display: block;
	margin-bottom: 5px;
}

.wp-block-aab-accordion-item {
	position: relative;
}

.aagb__accordion_container {
	.aab_autoplay_container {
		width: 2px;
		height: 100%;
		background: #c2b0b0; // default gray
		--before-bg: #c2b0b0; // default red
		position: absolute;
		top: 0;
		left: 0;
		transition: all 0.3s ease-in-out;

		&.active {
			height: 100%;
			width: 4px;
			background: black; // when accordion is active & autoplay is true
		}
	}
}

/* ðŸ”¹ Common styles */
.aab-progress-bar-container {
	position: absolute;
	overflow: hidden;
	z-index: 1;
}

.aab-progress-bar {
	position: absolute;
	transition: none;
	z-index: 2;
}

/* ðŸ”¹ Horizontal: left to right, bottom side */
.aab-progress-bar-horizontal {
	width: 100%;
	height: 4px;
	left: 0;
	bottom: 0;
}

.aab-progress-bar-horizontal .aab-progress-bar {
	width: 0%;
	height: 100%;
	left: 0;
	top: 0;
}

/* ðŸ”¹ Vertical: top to bottom, right side */
.aab-progress-bar-vertical {
	content: "";

	position: absolute;

	width: 4px;
	height: 100%;
	left: 0;
	top: 0;
}

.aab-progress-bar-vertical .aab-progress-bar {
	width: 4px;
	height: 0%;
	top: 0;
	bottom: 0;
}

//  ðŸ”¹ Group Accordion Feature Image
.aagb__group_accordion_container.has_img {
	display: flex;
	gap: 20px;

	.aab-feature-img {
		width: 50%;
	}
	.aab-feature-img img {
		width: 100%;
		height: auto;
		border-radius: 6px;
	}
	.aab-feature-img-enabled {
		width: 50%;
	}
}

// feature image for mobile and responsive
.aagb-feature-img-mobile {
	display: none;
}

@media (max-width: 992px) {
	.aagb__group_accordion_container.has_img {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 20px;

		.aab-feature-img {
			display: none !important;
		}
		.aab-feature-img-enabled {
			width: 100%;
		}
	}

	// feature image for mobile and responsive
	.aagb-feature-img-mobile {
		display: block;
		width: 100%;
		margin-top: 20px;

		img {
			width: 100%;
			height: auto;
			border-radius: 6px;
		}
	}
}


// show all and hide all button
.block-editor-block-list__block{
    .content-accordion__close-all{
        display: none;
    }
}

// accordion auto numbering
.aagb__accordion_auto_numbering{
    .aagb__accordion_number {
        font-size: 20px;
        font-weight: 800;
    }
}
.wp-block-aab-group-accordion {
	counter-reset: aab-accordion-counter;
}

.wp-block-aab-accordion-item .aagb__accordion_number::before {
	counter-increment: aab-accordion-counter;
	content: counter(aab-accordion-counter, decimal-leading-zero);
}

.aab-step-progress {
    width: 100%;
    height: 20px;
    // background: #dfd2d2;
    border-radius: 50px;
    margin-bottom: 20px;
    .aab-step-progress-bar{
        width: 0%;
        height: 100%;
        // background: #1570ec;
        border-radius: 50px;
        transition: width 0.3s ease-in-out;
    }
}